import { _decorator, Component, Node, Button, ButtonComponent, Enum, NodeEventType } from 'cc';
import { QuestionModal } from './Quiz/Modal/QuestionModal';
import { QuizComponent, QuizData, QuizType } from './Quiz/QuizComponent';
const { ccclass, property } = _decorator;

@ccclass('TestMenu')
export class TestMenu extends Component {

    @property(Button)
    button : Array<Button> = new Array<Button>();
    @property(QuestionModal)
    modal : QuestionModal = null;

    start() {
        this.button[0].node.on(Button.EventType.CLICK, function(){this.ClickFunction('Gesture')},this);
        this.button[1].node.on(Button.EventType.CLICK, function(){this.ClickFunction('Act')},this);
        this.button[2].node.on(Button.EventType.CLICK, function(){this.ClickFunction('Quiz')},this);
    }

    private ClickFunction(name : string){
        this.modal.Initialize(name);
    }
}

